import {
    v4 as uuidv4
} from "uuid";

export default {
    props: {
        model: String,
    },
    data() {
        return {
            editMode: false,
            doc: {
                _id: "",
                name: "",
                slideshow: [],
                productSections: [],
                alternateSections: [],
                description: "",
                status: false
            },
            allProductLists: [],
            loading: false,
            updated: false
        }
    },
    mounted() {
        this.fetchAllProductLists();
    },
    methods: {
        alternateImageListUpdated(list, index) {
            this.doc.alternateSections[index].image = list.length > 0 ? list[0].path : ""
        },
        removeProductSection(key) {
            this.doc.productSections.splice(key, 1);
        },
        addNewProductSection() {
            this.doc.productSections.push({
                heading: "",
                tagline: "",
                list: null
            })
        },
        addNewAlternateSection() {
            this.doc.alternateSections.push({
                heading: "",
                paragraph: "",
                image: "",
                key: uuidv4()
            })
        },
        removeAlternateSection(key) {
            this.doc.alternateSections.splice(key, 1);
        },
        async fetchAllProductLists() {
            const result = await this.$fetchCollection('product_lists');
            this.allProductLists = result.docs.map(({
                _id,
                name
            }) => {
                return {
                    name,
                    value: _id
                }
            });
            this.allProductLists.unshift({
                name: 'Select Product List',
                value: null
            });
        },
        slideshowListUpdated(list) {
            this.doc.slideshow = list;
        },
        async updateDocument(model, details, editMode) {
          
            this.loading = true;
            const result = await this.$updateDocument(this.model, this.doc, this.editMode);
            this.loading = false;

            if (!result.updated)
                return;

            this.$emit('updated');
            this.populateForm(result.doc);
            this.$flash(this);

        },
        async deleteDocument() {
            this.loading = true;
            const result = await this.$deleteDocument(this.model, this.doc._id);
            this.loading = false;

            if (!result.deleted)
                return;

            this.$emit('updated');
            this.resetForm();
            this.$flash(this);
        },
        populateForm(details) {
            const {
                _id,
                name,
                slideshow,
                productSections,
                alternateSections,
                description,
                status
            } = details;
            this.doc = {
                _id,
                name,
                slideshow,
                productSections,
                alternateSections,
                description,
                status
            };
            this.editMode = true;
            // this.$refs.imageUploader.assignImages();
        },
        closeForm() {
            this.resetForm();
            this.$emit('close');
        },
        resetForm() {
            // this.$refs.imageUploader.clearFileSelection();
            this.populateForm({
                _id: "",
                name: "",
                slideshow: [],
                productSections: [],
                alternateSections: [],
                description: "",
                status: false
            });
            this.editMode = false;
        }
    }
}
